.roadmap{
    padding: 4rem 6% ;
    background: #010A0F;
    color: white;
    position: relative;
    overflow: hidden;
}



.roadmap h1 {
    font-size: 3.5rem;
    margin-bottom: 30px;
 
}
.roadmap h1 span {
    color: var(--text);
}
.roadmap .title {
   
    font-size: 1.5rem;
    max-width: 500px;
   
} 
.map_list {
    max-width:500px;
    background: var(--text);
    padding: 30px;
    border-radius: 1rem;
    overflow: visible;
    z-index: 1;
    margin-top: 70px;
}
.map_1, .map_2 {
    display: flex;
    width: inherit;
    justify-content: space-between;
}
.map_1 h4 {
    font-size: 2.5rem;
    align-items: end;
}
.map_1 img {
    margin-top: -70px;
}
.map_2 {
    align-items: center;
}
.map_2 p {
    font-size: 0.9rem;
    width: 70%;
}
.map_2 a {
    font-size: 1.3rem;
    font-weight: 700;
    width: 45%;
    color:rgb(20,20,20);
    text-align: right;
}
.road_list {
    margin-top: 30px;
    line-height: 40px;
}

.road_list p div{
    height:15px;
    width: 15px;
    background: var(--green);
    padding: 3px;
}
.roadmap_img{
    position: absolute;
    bottom: 0;
    right: 0;
    transform: scale(0.9);
    margin-right: -110px;
}
.roadmap_img{
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: var(--green);
    width: 50%;
    height: inherit;
    backdrop-filter: blur(30%);
    z-index: 1;
}

.lis> span{
    color: var(--text);
    

}
.lis{
    line-height: 2rem;
}
.blur {
    width: 500px;
    height: 500px;
    position: absolute;
    top: 100px;
    right: 0;
    background: var(--text);
    filter: blur(250px);
    opacity: 0.7;
    margin-right: -100px;
    z-index: 0;
}


@media screen and (max-width: 950px){
    .blur1{
        opacity: 0.7;

    }
    .roadmap_img{
        display: none;
    }
}