.loans{
    padding: 4rem 6%;
    position: relative;
    overflow: hidden;
}

.loan_container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    gap: 3rem;
    justify-content: center;
}

.loan_box{
    width: 20rem;
    height: 30rem ;
    background: #FFFFFF;
box-shadow: 0px 4px 19px 2px rgba(0, 0, 0, 0.25);
border-radius: 30px;
display: flex;
flex-direction: column;

align-items: center;
padding: 4rem 2rem;
}

.loan_box >span{
    color: rgba(31, 164, 45, 0.7);
    font-size: 6rem;
}

.loan_box p{
    font-weight: 600;
font-size: 1.8rem;
text-align: center;
color: #010A0F;
}

.loan_box article{
    font-size: 1rem;
    text-align: left;
}



@media screen and (max-width: 550px) {
   .loan_box{
    width: 70%;
    height: 27rem;
   }
} 