.utility{
    padding: 4rem 6%;
    background: #010A0F;
    color: white;
    position: relative;
    overflow: hidden;
}

.blur {
    width: 500px;
    height: 500px;
    position: absolute;
    top: 100px;
    right: 0;
    background: var(--text);
    filter: blur(250px);
    margin-right: -100px;
}

.utility h2{
    font-weight: 700;
    font-size: 3rem;
    text-align: center;
}

.utility_container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 4rem;
    padding: 2.5rem ;
}

.utility_text h4{
    font-style: normal;
    font-weight: 600;
    font-size: 2rem;
}

.utility_text p{
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 45px;
}

.utility_container img{
    width: 23rem;
    height: 21rem;
    z-index: 1;
}


@media screen and (max-width: 750px) {
    .utility_container img{
        display: none;
    }

    .utility_container{
        position: relative;

    }
    .utility_container::before{
        content: "";
        background-image: url(../Images/dollar.png);
        background-size: cover;

        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: 0.7;
        
    }
    .utility_text{
        position: relative;
    }
    .blur{
        display: none;
    }
}