.home{
    padding-top: 5rem;
    background: #010A0F;
    height: 100vh;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
}

.home_container{
    background: url(../Images/Rectangle2.png) #010a0f3f;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 70vh;
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 2rem;
    text-align: center;
    

}

.heading{
    
    background: radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(31, 164, 45, 0.4) 0%, rgba(255, 255, 255, 0) 100%);
    backdrop-filter: blur(21px);
    border-radius: 50px;
    padding: 1rem 0.8rem;
    border: 3px solid;
    border-image-source: linear-gradient(114.55deg, rgba(31, 164, 45, 0.8) 2.13%, rgba(31, 164, 45, 0) 98.14%),
    radial-gradient(97.09% 224.61% at 1.38% 96.94%, rgba(239, 239, 239, 0) 0%, #EFEFEF 100%) 
    radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(239, 239, 239, 0) 0%, #EFEFEF 100%) ;
    margin-bottom: -2rem;
    z-index: 1;


}

.head{
    padding-top: 1rem ;
    font-style: normal;
    font-weight: 800;
    font-size: 5rem;
    line-height: 5rem



}

.head span{
    color: var(--text);
}
.p_body{
    font-weight: 600;
    font-size: 1rem;
    text-align: center;
    padding-top: 1.5rem;
}

