.community_container{
    padding: 4rem 6% ;
    background: #010A0F;
    color: white;
    position: relative;
    overflow: hidden;
    text-align: center;
}

.community_container h2{
    font-size: 3rem;
    font-weight: 700;
    padding-bottom: 2rem;

}

.community_container h2 span{
    color: var(--text);
}

.community_container> p{
    font-weight: 600;
    font-size: 1.3rem;
    padding: 0 3rem;
}
 .links{
    display: flex;
    flex-direction: row;
    flex-wrap:wrap ;
    gap: 4rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding-top: 3rem;

 }

.links a{
    max-width: 25rem ;
    display: flex;
    flex-direction: row;
    background: radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(31, 164, 45, 0.4) 0%, rgba(255, 255, 255, 0) 100%);
    backdrop-filter: blur(21px);
    border-radius: 50px;justify-content: center;
    align-items: center;
    border: 1px solid;
    border-image-source: linear-gradient(114.55deg, rgba(31, 164, 45, 0.8) 2.13%, rgba(31, 164, 45, 0) 98.14%),
    radial-gradient(97.09% 224.61% at 1.38% 96.94%, rgba(239, 239, 239, 0) 0%, #EFEFEF 100%) 
    radial-gradient(97.57% 210.75% at 0.9% 2.98%, rgba(239, 239, 239, 0) 0%, #EFEFEF 100%) ;

}

.link_div{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: left;
}

.links a img{
    width: 5rem;
    transform: scale(1.8);
    height: 5rem;
}

.link_text h4{
    font-weight: 700px;
    font-size: 1.5rem;
}

.link_text p{
    font-size: 0.8rem;
   
}
.link_text {
    width: 80%;
    
}

.footer {
    background: #FFFFFF;
    color: var(--text);
    padding: 2rem 3rem;
}

.footer h2{
    font-size: 3rem;
}



@media screen and (max-width: 550px) {
    .links a img{
        width: 5rem;
        transform: scale(1.4);
        height: 5rem;
    }
    
 } 