@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap');

a{
  text-decoration: none;
  color: white;
}
*{
  margin: 0;
  font-family: 'Poppins', sans-serif;
  padding: 0;
  outline: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  scroll-behavior: smooth;
  
}

.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}   

 

.gradient_bg{

  background:-moz-linear-gradient(88deg, rgba(1, 10, 15, 1) 0%, rgba(1, 10, 15, 1) 74%, rgba(31, 164, 45, 0.79) 100%); 

  /* safari 5.1+,chrome 10+ */
  background:-webkit-linear-gradient(88deg, rgba(1, 10, 15, 1) 0%, rgba(1, 10, 15, 1) 74%, rgba(31, 164, 45, 0.79) 100%);

  /* opera 11.10+ */
  background:-o-linear-gradient(88deg, rgba(1, 10, 15, 1) 0%, rgba(1, 10, 15, 1) 74%, rgba(31, 164, 45, 0.79) 100%);

  /* ie 6-9 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1FA42D', endColorstr='#010A0F', GradientType=0 );

  /* ie 10+ */
  background:-ms-linear-gradient(88deg, rgba(1, 10, 15, 1) 0%, rgba(1, 10, 15, 1) 74%, rgba(31, 164, 45, 0.79) 100%);

  /* global 94%+ browsers support */
  background:linear-gradient(88deg, rgba(1, 10, 15, 1) 0%, rgba(1, 10, 15, 1) 74%, rgba(31, 164, 45, 0.79) 100%);

}
   
@media screen and (max-width: 1024px){
    
  html{
      font-size: 85%;
  }
}

@media screen and (max-width: 885px){
  
  html{
      font-size: 80%;
  }
  
}


@media screen and (max-width: 850px){
  
  html{
    font-size: 75%;
  }
}



@media screen and (max-width: 640px){
  
  html{
      font-size: 68%;
  }

}

@media screen and (max-width: 550px){
  
  html{
      font-size: 65%;
  }

 
}