.loan{

    background: #010A0F;
    color: white;
    text-align: center;
    padding: 4rem 6% 6rem 6%;
}
.loan h2{
    font-style: normal;
    font-weight: 700;
    font-size: 3rem;
    padding-bottom: 1.5rem;

}

.loan p{
    font-weight: 600;
    font-size: 1.3rem;
    text-align: center;
    padding-bottom: 1rem;
}

.loan p span{
    color: var(--text);
}

.loan_link {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    padding-top: 2rem;
    gap: 3rem;

}

.loan_link a{
    padding: 0.8rem 1rem;
    text-align: center;
    background: #FFFFFF;
    color: var(--text);
    font-weight: 600;
    font-size: 1.5rem;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    min-width: 16rem;
}

.loan_link a:nth-child(2){
    background: var(--text);
    color: white;
}

